.landing{min-height:50vh;justify-content:center}@media screen and (min-width: 580px){.landing{padding-right:3rem}}.landing p{padding:.8rem 0 0}.landing-image{padding:3.5rem}@media screen and (max-width: 820px){.landing-image{display:none}}

.offer{margin-top:.4rem;display:flex;align-items:center;width:100%;padding:0 1rem;border-radius:8px;list-style:none;-webkit-transition:background-color .2s ease-in-out;transition:background-color .2s ease-in-out}.offer:hover{background-color:rgba(0,0,0,0.02)}.offer span{padding:.7em 0}.offer div{margin-left:auto}

main{display:flex;flex-wrap:wrap;width:100%}.offer-container-header{display:flex;align-items:flex-end;padding-bottom:1rem;margin-bottom:1rem;border-bottom:1px solid rgba(0,0,0,0.05)}.offer-container-header h2{margin-bottom:0}.offer-container-header button{margin-left:auto}

